import { AdminUrlOrder } from './../../../utils/URL/admin-url/admin-order/seller-url-order';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { ApiResult } from 'src/app/core/models/common/ApiResult';
import { PagedResult } from 'src/app/core/models/common/page/PagedResult';
import { OrderPagingRequest } from 'src/app/core/models/seller/order/request/OrderPagingRequest';

import { OrderResponse } from 'src/app/core/models/seller/order/response/OrderResponse';
import { OrderAndOrderDetailResponse } from 'src/app/core/models/admin/order/response/OrderAndOrderDetailResponse';

@Injectable({
  providedIn: 'root'
})
export class AdminOrderService {
  languageCode = 'vn';
	categoryId = 1;
	constructor(private urlString: AdminUrlOrder, private http: HttpClient) {}
	getOrders(request: OrderPagingRequest): Observable<ApiResult<PagedResult<OrderResponse>>> {
		request.languageCode = this.languageCode;
		return this.http.get<ApiResult<PagedResult<OrderResponse>>>(this.urlString.getOrderByOrderStatus(request));
	}
	postConfirmOrder(orderId: number): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.urlString.postConfirmOrder(), orderId);
	}
	postDeleteOrder(orderId: number): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.urlString.postCanceledOrder(), orderId);
	}
	getOrderDetail(orderId: number): Observable<ApiResult<OrderAndOrderDetailResponse>> {
		return this.http.get<ApiResult<OrderAndOrderDetailResponse>>(this.urlString.getOrderDetails(orderId));
	}
}
