import { AdminUrlBase } from './../admin-url-base';
import {Injectable} from '@angular/core';
import {OrderPagingRequest} from 'src/app/core/models/seller/order/request/OrderPagingRequest';
@Injectable({
	providedIn: 'root'
})
export class AdminUrlOrder extends AdminUrlBase {
	constructor() {
		super();
	}
	getOrderByOrderStatus(orderPagingRequest: OrderPagingRequest): string {
		return (
			this.adminUrl +
			`/order/order-status?orderRequestType=${orderPagingRequest.orderRequestType}&pageIndex=${orderPagingRequest.pageIndex}&pageSize=${orderPagingRequest.pageSize}&languageCode=${orderPagingRequest.languageCode}`
		);
	}
	postConfirmOrder() {
		return this.adminUrl + `/order/confirm`;
	}
	postCanceledOrder() {
		return this.adminUrl + `/order/delete`;
	}
	getOrderDetails(orderId: number) {
		return this.adminUrl + `/order/order-detail/` + orderId;
	}
}
